.card {
    height: 200px;
    width: 150px;
    border: red solid 5px;
    background: white;
}

img {
    height: 150px;
    width: 100px;
    -webkit-align-content: center;
            align-content: center;
}

.actualImg {
    display: block;
    margin-top: 16px;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}



.wrapper {
  padding-top: 50px;
  background: black;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.headerImg {
    width: 100%;
    height: 250px;
}

.footer {
    background: red;
    height: 25px;
}

a {
    color: white;
}
body {
  background: black;
}
h2, .title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
  color: red;
}

.grid-container {
  display: grid;
  grid-gap: 16px;  
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

